// @import "reset";
@import "generated/sprite-svg";
@import "mixins";
@import "vars";
@font-face {
  font-family: 'ACaslonPro-Regular';
  src: url('../fonts/Caslon/ACaslonPro-Regular.eot');
  src: url('../fonts/Caslon/ACaslonPro-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Caslon/ACaslonPro-Regular.woff2') format('woff2'), url('../fonts/Caslon/ACaslonPro-Regular.woff') format('woff'), url('../fonts/Caslon/ACaslonPro-Regular.svg#o Regular') format('svg');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-00FE;
}

html {
  box-sizing: border-box
}

*,
::after,
::before {
  box-sizing: inherit
}

:root {
  font-family: $font-primary;
  // line-height: 1.6
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch
}

body,
html {
  height: 100%
}

form {
  font-family: inherit;
  .custom-select {
    border-radius: 2px;
  }
  .btn-primary {
    min-width: 150px;
  }
}

.btn {
  border-radius: 2px;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s ease;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}

.custom-link {
  text-decoration: underline;
  &:hover {
    text-decoration: none;
  }
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  font-family: $font-primary;
  color: #ebebeb;
  background-color: #3b3b3b;
  overflow: hidden;
}

// HEADER
.header {
  padding: 13px 0;
  .navbar {
    align-items: flex-start;
    padding-left: 0;
    padding-right: 0;
  }
  .navbar-nav {
    @media only screen and (max-width: 768px) {
      align-items: center;
      padding-top: 20px;
    }
  }
  .navbar-brand img {
    max-width: 189px;
    @media only screen and (max-width: 480px) {
      max-width: 127px;
    }
  }
  .nav-link {
    font-family: $font-primary;
    font-size: 11px;
    font-weight: 300;
    letter-spacing: 0.8px;
    color: #ebebeb;
    text-transform: uppercase;
    &.active {
      font-weight: 700;
    }
  }
  .navbar-toggler {
    @media only screen and (max-width: 992px) {
      display: flex;
      align-items: flex-end;
    }
    border: none;
    color: #ffffff;
  }
  .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(255,255,255)' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  }
  .navbar-toggler__text {
    padding-bottom: 2px;
    padding-left: 10px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
  }
}

// CONTENT SECTION
.content {
  padding-top: 117px;
  .content__title {
    position: relative;
    padding-bottom: 25px;
    font-family: "ACaslonPro-Regular";
    font-size: 48px;
    letter-spacing: 0.5px;
    text-align: center;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background-color: #c1bf8e;
    }
  }
  .content__title-desc {
    padding: 20px 0 31px;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.67;
    letter-spacing: 0.2px;
    text-align: center;
    color: #ffffff;
  }
  .grid__item {
    margin-bottom: 47px;
    // padding: 28px 0 36px;
    padding: 28px 0 45px;
    background-color: #d5d0d0;
  }
  .grid__icon {
    text-align: center;
    .icon {
      width: 42px;
      height: 42px;
    }
  }
  .grid__title {
    margin-top: 9px;
    margin-bottom: 9px;
    font-family: "ACaslonPro-Regular", serif;
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.2px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #000000;
  }
  .grid__desc {
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.1px;
    text-align: center;
    color: #3a2e2e;
  }
  .grid__picture {
    margin-top: 67px;
    img {
      width: 100%;
    }
  }
  // ACCORDION
  .accordion {
    padding-top: 48px;
    .accordion__rule {
      width: 100%;
      margin-bottom: 15px;
      font-size: 18px;
      font-weight: 300;
      line-height: 1.67;
      letter-spacing: 0.2px;
      text-align: center;
      color: #ebebeb;
    }
    .card {
      margin-top: 50px;
      color: #ebebeb;
      background-color: transparent;
      border: none;
    }
    .card-header {
      position: relative;
      padding-left: 0;
      padding-right: 10px;
      background-color: transparent;
      border-bottom: solid 1px #c1bf8e;
      a[aria-expanded="true"] {
        .card__plus {
          opacity: 0;
        }
      }
      .card__minus {
        position: absolute;
        right: 10px;
        top: 0;
        bottom: 0;
        display: block;
        margin: auto;
        width: 17px;
        height: 4px;
        background-color: #fff;
      }
      .card__plus {
        position: absolute;
        right: 16px;
        top: 0;
        bottom: 0;
        display: block;
        margin: auto;
        width: 4px;
        height: 16px;
        background-color: #fff;
        transition: all 0.2s ease;
      }
      a {
        display: block;
        width: 100%;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.67;
        letter-spacing: 0.2px;
      }
    }
    .card-body {
      padding: 58px 0 11px;
      font-size: 12px;
    }
  }
}

// museum foto
.museum-foto {
  @media only screen and (min-width: 768px) {
    padding: 10px 0 0;
  }
  img {
    min-height: 354px;
    display: block;
    width: 100%;
    opacity: 0.9;
    mix-blend-mode: lighten;
    object-fit: fill
  }
}

// subscribe  
.subscribe {
  padding: 55px 0 54px;
  @media only screen and (max-width: 480px) {
    padding-bottom: 30px;
  }
  background: #2f2f2f;
  .subscribe__title {
    font-size: 17px;
    font-weight: 300;
    letter-spacing: 3.4px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    @media only screen and (min-width: 480px) {
      br {
        display: none;
      }
    }
    @media only screen and (max-width: 480px) {
      line-height: 1.76;
      .separator {
        display: none;
      }
    }
  }
  .subscribe__form {
    margin-top: 23px;
  }
  .form__row {
    display: flex;
    @media only screen and (max-width: 480px) {
      flex-direction: column;
    }
  }
  .subscribe__input,
  .subscribe__submit {
    height: 37px;
    margin-bottom: 4px;
    border-radius: 2px;
    border: none;
  }
  .subscribe__input {
    width: 293px;
    padding-left: 10px;
  }
  input::placeholder {
    font-size: 14px;
    color: #8f8f8f;
    text-transform: uppercase;
    @media only screen and (max-width: 480px) {
      text-align: center;
    }
  }
  .subscribe__submit {
    width: 158px;
    margin-left: 4px;
    font-size: 14px;
    color: #222222;
    text-transform: uppercase;
    @media only screen and (max-width: 480px) {
      margin-left: 0;
      width: 100%;
    }
  }
  .subscribe__info {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 17px;
    font-size: 11px;
    color: #9f9f9f;
    .info__icon {
      display: flex;
      align-items: center;
      margin-right: 3px;
      .icon {
        width: 11px;
        height: 11px;
      }
    }
  }
}

// FOOTER
.footer {
  padding: 47px 0 40px;
  color: #3b3b3b;
  background-color: #dadada;
  .footer__logo {
    display: flex;
    justify-content: center;
    @media only screen and (min-width: 768px) {
      margin-bottom: 10px;
    }
  }
  .footer__nav {
    font-size: 10px;
    letter-spacing: 0.8px;
    text-align: center;
    color: #3b3b3b;
    .navbar {
      padding: 4px 0;
    }
    .navbar-nav {
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      justify-content: center;
      font-size: 10px;
      @media only screen and (max-width: 768px) {
        flex-direction: column;
        font-size: 18px;
        padding: 25px 0;
      }
    }
    .nav-link {
      padding-left: 14px;
      padding-right: 14px;
      text-transform: uppercase;
      color: #3b3b3b;
    }
  }
  .underlined {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -4px;
      display: block;
      width: 100%;
      height: 2px;
      background-color: #3b3b3b;
    }
  }
  .navbar--secondary {
    .underlined:after {
      @media only screen and (min-width: 768px) {
        opacity: 0;
      }
    }
    @media only screen and (max-width: 768px) {
      .nav-link {
        font-size: 14px;
      }
    }
  }
  .footer__copy {
    margin-top: 23px;
    font-size: 10px;
    font-weight: 300;
    line-height: 1.6;
    letter-spacing: 0.8px;
    text-align: center;
  }
}

// SEARCH PAGE
.search {
  font-weight: 400;
  .content__title-desc {
    font-size: 1rem;
  }
  &__form {
    margin-top: 50px;
    padding: 70px 0;
    color: #000;
    background-color: #fafafa;
    @media only screen and (max-width: 480px) {
      .custom-select {
        margin-bottom: 10px;
      }
    }
  }
  &__selection {
    display: flex;
    flex-wrap: wrap;
  }
  &__radio-block {
    margin-right: 30px;
  }
  .btn-submit {
    display: block;
    margin: 20px auto 40px;
  }
  .search__options {
    font-size: 12px;
    .btn-link {
      padding: 0 5px;
      color: #8f8f8f;
      font-size: inherit;
    }
    .is-active {
      color: #2f2f2f
    }
  }
  &__bottom-tip {
    margin: 20px 0;
    font-weight: 300;
  }
}